<template>
    <div id="header">
        <div class="wf-table">
            <div id="branding" class="wf-td bit-logo-bar" style="padding-left: 50px;padding-right: 50px;">
                <span style="color: rgb(0, 0, 0); font-family: georgia; font-size: 34px;"><strong>Chgan</strong></span>
            </div>
            <div class="wf-td" style="width:100%">
                <el-menu
                    :default-active="activeIndex"
                    class="el-menu-demo"
                    mode="horizontal"
                    @select="handleSelect"
                >
                    <el-menu-item index="1">首页</el-menu-item>
                    <el-menu-item index="2">关于我</el-menu-item>
                    <el-submenu index="3">
                        <template slot="title">服务大厅</template>
                        <el-menu-item index="3-1">福建省12333公共服务平台</el-menu-item>
                        <el-menu-item index="3-2">福建省住房公积金网上办事大厅</el-menu-item>
                        <el-menu-item index="3-3">福建省计算机技术与软件专业资格水平考试</el-menu-item>
                    </el-submenu>
                    <el-menu-item index="40">我的笔记</el-menu-item>
                    <el-menu-item index="99">我的博客</el-menu-item>
                </el-menu>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      msg: 'Welcome To Wu Chgan Personal Homepage',
      activeIndex: '1'
    }
  },
  created () {
    this.activeIndex = this.$route.query.index || '1'
  },
  methods: {
    handleSelect (key, keyPath) {
      if (key === '99') {
        // 跳转到 blog
        window.open('https://blog.csdn.net/qq_33384191')
      } else if (key === '2') {
        this.$router.push(
          {
            path: '/resume',
            query: {
              index: '2'
            }
          }
        )
      } else if (key === '1') {
        this.$router.push(
          {
            path: '/',
            query: {
              index: '1'
            }
          }
        )
      } else if (key === '3-1') {
        window.open('http://220.160.52.229:9001/ggfwwt-person/person/home')
      } else if (key === '3-2') {
          //
          window.open('https://cx.fjszgjj.com/wsyyt/')
      } else if (key === '3-3') {
        //   window.open('http://soft.feitc.org.cn/soft/authorize/view/login/login.html')
            window.open('https://bm.ruankao.org.cn/sign/in?time=2')
      } else if (key === '40') {
          this.$router.push(
            {
                path: '/mynotes',
                query: {
                    index: '40'
                }
            }
          )
      }
    }
  }
}
</script>

<style scoped>
#branding {
    padding: 10px 0;
    position: relative;
}
.wf-td {
    display: table-cell;
    vertical-align: middle;
}
.wf-table {
    display: table;
    width: 100%;
    padding-bottom: 15px;
}
.el-menu.el-menu--horizontal {
    border-bottom: solid 0px #fff;
}
.el-menu--horizontal>.el-menu-item {
    color: #000;
    font-size: 18px;
}
#header {
    position: fixed;
    background: #fff none repeat center top;
    border-bottom: 1px 0 rgba(0,0,0,0.2);
    z-index: 1000000;
    padding-top: 8px;
    width: 100%
}
.el-menu--horizontal>>>.el-submenu .el-submenu__title {
    color: #000;
    font-size: 18px;
}
</style>

<style>
/* .el-menu--horizontal>.el-submenu .el-submenu__title {
    color: #000;
    font-size: 18px;
} */
</style>



