<template>
    <div>
        <Head/>
        <!-- 笔记 -->
        <div class="body">
            <el-container>
                <!-- 左侧边栏 -->
                <el-aside class="aside-menu">
                    <!-- 菜单 -->
                    <el-menu
                        background-color="#fff"
                        text-color="#000"
                        active-text-color="#FF0000"
                        default-active="200-1-1"
                        :unique-opened=true
                        @select="handleSelect"
                    >
                        <!-- 子菜单 -->
                        <el-submenu v-for="(menuData, index) in menuDataList" :key="index+''" :index="index+''">
                            <template slot="title"><i :class=menuData.icon></i><span>{{menuData.title}}</span></template>
                            <el-menu-item-group>
                                <el-menu-item v-for="(children, cidx) in menuData.children" :key="index+''" :index=children.index>
                                    {{children.itemName}}
                                </el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <!-- 右侧容器 -->
                <el-container>
                    <el-main class="main">
                        <!-- README.md文件 -->
                        <template>
                            <div v-if="showType === 'md'" class="markdown-body markdown-body-wf">
                                <vue-markdown v-html="readmeHtml"></vue-markdown>
                                <!-- <mavon-editor
                                    v-model="content"
                                    ref="md"
                                    @change="change"
                                /> -->
                            </div>
                            <!-- PDF 文件 -->
                            <div v-else-if="showType === 'pdf'">
                                <pdf
                                    :src="pdfurl"
                                    v-for="pdfCurrentPage in pdfPageCount"
                                    :key="pdfCurrentPage"
                                    :page="pdfCurrentPage"
                                >
                                </pdf>
                            </div>
                        </template>
                        <template>
                            <el-backtop></el-backtop>
                        </template>
                    </el-main>
                </el-container>
            </el-container>
        </div>
    </div>
</template>

<style>
    .body {
        padding-top: 85px;
    }

    .main {
        border-top: 1px solid #E6E6FA;
        border-left: 1px solid #E6E6FA;
        padding-left: 320px !important;
    }

    .aside-menu {
        width: 250px;
        background-color: rgb(255, 255, 255);
        text-align: left;
        border: 1px solid #E6E6FA;
        position: fixed;
        min-height: 100%;
        font-weight: bold;
    }

    .el-header {
        background-color: #B3C0D1;
        color: #333;
        line-height: 60px;
    }

    .el-aside {
        color: #333;
    }

    .el-menu {
        border-right: solid 0px #e6e6e6;
        border-right-width: 0px;
        border-right-style: solid;
        border-right-color: rgb(230, 230, 230);
        list-style: none;
        position: relative;
        margin: 0;
        padding-left: 0;
        background-color: #FFF;
    }

    .markdown-body-wf {
        min-height: 800px;
        text-align: left;
    }

</style>

<script>
import Head from '@/components/Header';
import VueMarkdown from 'vue-markdown';
import SpringBoogReadme from '../assets/md/spring-boot.md';
import SpringAnnotationReadme from '../assets/md/spring-annotation.md';
import GitOperationReadme from '../assets/md/git-operation.md';
import JavaCoreTopic1Readme from '../assets/md/java-core-topic1.md';
import JavaCoreTopic2Readme from '../assets/md/java-core-topic2.md';
// 导入组件 及 组件样式
import { mavonEditor } from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import 'github-markdown-css/github-markdown.css';
import pdf from 'vue-pdf';
  export default {
    components: {
        Head, VueMarkdown, mavonEditor, pdf
    },
    data() {
      return {
        menuDataList: [
            {
                index: '30-1',
                icon: 'el-icon-document',
                title: 'Git',
                children: [
                    {
                        index: '30-1-1',
                        itemName: 'Git常用操作',
                        file: GitOperationReadme,
                        type: 'md'
                    }
                ]
            },
            {
                index: '50-1',
                icon: 'el-icon-document',
                title: 'Java',
                children: [
                    {
                        index: '50-10-1',
                        itemName: 'Java核心知识（基础）',
                        file: JavaCoreTopic1Readme,
                        type: 'md'
                    },
                    {
                        index: '50-20-1',
                        itemName: 'Java核心知识（容器）',
                        file: JavaCoreTopic2Readme,
                        type: 'md'
                    }
                ]
            },
            {
                index: '100-1',
                icon: 'el-icon-document',
                title: 'Spring',
                children: [
                    {
                        index: '100-1-1',
                        itemName: 'Spring Annotation',
                        file: SpringAnnotationReadme,
                        type: 'md'
                    }
                ]
            },
            {
                index: '200-1',
                icon: 'el-icon-document',
                title: 'Spring Boot',
                children: [
                    {
                        index: '200-1-1',
                        itemName: 'Spring Boot笔记',
                        file: SpringBoogReadme,
                        type: 'md'
                    }
                ]
            },
            {
                index: '300-1',
                icon: 'el-icon-document',
                title: 'Design Pattern',
                children: [
                    {
                        index: '300-1-1',
                        itemName: 'Design Pattern笔记',
                        file: './static/pdf/ssghspdp.pdf',
                        type: 'pdf',
                        pageCount: 393,
                        currentPage: 1
                    }
                ]
            }
        ],
        springBoogReadme: SpringBoogReadme,
        content: SpringBoogReadme, // 输入的markdown
        html: '', // 及时转的html
        readmeHtml: SpringBoogReadme,
        pdfurl: '',
        pdfPageCount: 0,
        pdfCurrentPage: 1,
        showType: 'md'
      }
    },
    mounted() {
        this.getPdfNumPages();
    },
    methods: {
        handleSelect (key, keyPath) {
            // 浏览器滚动
            window.scrollTo(0, 0);
            // 根绝key获取 itemName 即为readme文件
            // const readme = this.getReadmeNameByIndex(key);
            const file = this.getFileByIndex(key);
            // 根据readme文件 渲染文件
            // this.renderView(readme);
            if (file.type === 'md') {
                this.renderView(file.name);
            } else if (file.type === 'pdf') {
                this.renderPdfView(file.name, file.pageCount, file.currentPage);
            }
        },
        getReadmeNameByIndex (index) {
            let readme;
            for (const item of this.menuDataList) {
                for (const childrenItem of item.children) {
                    if (index === childrenItem.index) {
                        readme = childrenItem.file;
                        break;
                    }
                }
            }
            return readme;
        },
        getFileByIndex (index) {
            const file = {};
            let readme;
            for (const item of this.menuDataList) {
                for (const childrenItem of item.children) {
                    if (index === childrenItem.index) {
                        readme = childrenItem.file;
                        file.name = readme;
                        file.type = childrenItem.type;
                        file.pageCount = childrenItem.pageCount;
                        file.currentPage = childrenItem.currentPage;
                        this.showType = file.type;
                        break;
                    }
                }
            }
            return file;
        },
        renderView (readme) {
            this.readmeHtml = readme;
        },
        renderPdfView (path, pageCount, currentPage) {
            this.pdfPageCount = pageCount;
            this.pdfCurrentPage = currentPage;
            this.pdfurl = pdf.createLoadingTask(path);
        },
        // 所有操作都会被解析重新渲染
        change(value, render) {
            // render 为 markdown 解析后的结果[html]
            this.html = render;
        },
        getPdfNumPages() {
            const loadingTask = pdf.createLoadingTask(this.pdfUrl)
            loadingTask.promise.then(pdf => {
                this.pdfCurrentPage = pdf.pdfPageCount
            }).catch(err => {
                console.error('pdf 加载失败', err);
            })
        }
    }
  };
</script>


